<template>
  <section class="v-container" :class="{ 'is-vertical' : isVertical}">
    <slot></slot>
  </section>
</template>

<script>
export default {
  name: 'VContainer',
  componentName: 'VContainer',
  props: {
    direction: String
  },
  computed : {
    isVertical () {
      if (this.direction === 'vertical') {
        return true
      } else if (this.direction === 'horizontal') {
        return false
      }
      return this.$slots && this.$slots.default
        ? this.$slots.default.some(vnode => {
          const tag = vnode.componentOptions && vnode.componentOptions.tag
          return tag === 'v-header' || tag === 'v-footer'
        })
        : false
    }
  }
}
</script>

<style>

</style>



